import Vue from 'vue'
import Router from 'vue-router'
import One from '@/components/one'
import two from '@/components/two'
import three from '@/components/three'
import four from '@/components/four'
import five from '@/components/five'
import activity from '@/components/activity/activity'
import activityIn from '@/components/activity/activityIn'
import lingqu from '@/components/activity/lingqu'
import fenxiang from '@/components/activity/fenxiang'
import getit from '@/components/activity/getit'

Vue.use(Router)

export default new Router({
  routes: [
	  {
      path: '/',
      name: 'activity',
      component: activity
    },
	    {
      path: '/activityIn',
      name: 'activityIn',
      component: activityIn
    },
	   {
      path: '/lingqu',
      name: 'lingqu',
      component: lingqu
    },
	   {
      path: '/getit',
      name: 'getit',
      component: getit
    },
	   {
      path: '/fenxiang',
      name: 'fenxiang',
      component: fenxiang
    },
    {
      path: '/one',
      name: 'One',
      component: One
    },
	 {
      path: '/two',
      name: 'two',
      component: two
    },
	  {
      path: '/three',
      name: 'three',
      component: three
    },
	    {
      path: '/four',
      name: 'four',
      component: four
    },
	{
      path: '/five',
      name: 'five',
      component: five
    }
  ]
})



