<template>
    <div>
    <v-top></v-top>
    <div class="one-box">
    	<div class="one-top">
    		1、星期一的早上，你高兴的骑着小蜜蜂去上班。到公司门口后，你把小蜜蜂停放在以下哪个位置不属于违停？
    	</div>
    	<div class="one-banner">
    		<img src="../../static/img/one-banner.png" alt="">
    	</div>
    	<div class="one-cheeked">
    		<ul  v-for="(i,index) in onearr">
    			<li>
<!--    				<img src="../../static/img/q1.png" alt="">-->
    				<input type="radio" name="one" index="A">
    				
    				<span>{{i.A}}</span>
    			</li>
    			<li>
    				<input type="radio" name="one">
    				<span>{{i.B}}</span>
    			</li>
    			<li>
    				<input type="radio" name="one">
    				<span>{{i.C}}</span>
    			</li>
    		</ul>
    	</div>
    	<div class="one-btn">
    		<div>
    			<a href=""><img src="../../static/img/shangH.png" alt=""></a>
    			<img src="../../static/img/xia.png" alt="" @click="xia()">
    		</div>
    	</div>
<!--    	进度条-->
    	<div class="progress">
			<div class="progress-bar progress-bar-danger active progress-bar-striped" style="width: 20%">
				<span></span>
			</div>
    	</div>
    	
			<!--    组件-->
			<v-cuo :lists="onearr"  v-show="onearr[0].cuo"></v-cuo> 
<!--			onearr[0].cuo管理cuo组件的属性-->
			<v-kong :lists="onearr"  v-show="onearr[0].kong"></v-kong>
<!--			onearr[0].kong管理kong组件的属性-->
			<v-dui :lists="onearr"  v-show="onearr[0].dui"></v-dui>
<!--   			onearr[0].dui管理dui组件的属性-->
    </div>
    	<div class="foot">
    		<img src="../../static/img/foot.png" alt="">
    	</div>
    </div>
</template>

<script>
	import cuo from "../module/cuo"
	import kong from "../module/kong"
	import dui from "../module/dui"
	import top from "../module/top"
    export default {
        data () {
            return {
				onearr:[
					{
						"A":"A、非机动车上。",
						"B":"B、公司院内",
						"C":"C、不阻碍交通的公共停车区域",
						"success":"C",
						"url":"/two",
						"cuo":false,
						"kong":false,
						"dui":false
					}
				]
            }
        },
        components: {
			"v-cuo":cuo,
			"v-kong":kong,
			"v-dui":dui,
			"v-top":top,
        },
		methods:{
			xia(){
				var Ali = document.getElementsByName("one"); //所有的单选按钮
					if(Ali[0].checked){//第一个单选选中
						console.log("错误")
						this.onearr[0].cuo = true;
						return
					}else if(Ali[1].checked){	//第二个单选选中	
						console.log("错误")
						this.onearr[0].cuo = true;
						return
					}else if(Ali[2].checked){//第三个单选选中
						console.log("正确")
						this.onearr[0].dui = true;
						return
					}else{//不选
						console.log("空")
						this.onearr[0].kong = true;
						return
					}
			}
		}
    }
</script>

<style scoped>
	.one-box{
		padding: 0 0.85rem 0 0.85rem ;
/*		font-size: 10px;*/
	}
	.one-top{
		
	}
	.one-banner{
		
	}
	.one-banner>img{
		width: 100%;
/*		margin-top: 0.85rem;*/
	}
	.one-cheeked{
		
	}
	.one-cheeked>ul{
		margin-top: 0.9rem;
	}
	.one-cheeked>ul>li{
		padding: 0.8rem 0;
		border-top: 1px solid #f4f4f4;
		position: relative;
	}
	.one-cheeked>ul:last-child{
		border-bottom: 1px solid #f4f4f4;
	}
	.one-btn{
		margin-top: 0.8rem;
	}
	.one-btn>div{
		display: flex;
		align-items: center;
		justify-content: space-around
	}
	.one-btn img{
		width: 6.3rem;
	}
	input{
/*		opacity: 0;*/
		margin-right: 1rem;
		position: relative;
		z-index: 1;
	}
	.one-cheeked>ul>li img{
		width: 1rem;
		height: 1rem;
		position: absolute;
	}
	.progress-bar-danger{
		background: repeating-linear-gradient(45deg,yellow 10%,#fff 20%);
         border-radius: 1.25rem;
	}
	.progress{
		margin-top: 0.65rem;
		height: 0.6rem;
	
	}
	.foot{
		width: 100%;
		height: 2.17rem;
		position: absolute;
		bottom: 0;
	}
	.foot>img{
		width: 100%;
		height: 100%;
	}
</style>


