<template lang="html">
  <div class="">
      <top></top>
  <div class="app">
    <div class="content">
      <p>
        3、 到公司后，同事跟你说，他将小蜜蜂停在公司楼下的地下停车场了。你告诉他这属于违停，它将会被？
      </p>
    </div>
    <img class="img" src="../../static/img/1.png" alt="" />
    <div class="" v-for="key in threearr">
      <div class="nav">
        <div class="">
          <input type="radio" name="name" value="">
          <!-- <img src="../../static/img/椭圆-1.png" alt="" /> -->
        </div>
        <span>
        {{key.A}}
        </span>
      </div>
      <div class="nav">
        <div class="">
          <input type="radio" name="name" value="">
          <!-- <img src="../../static/img/椭圆-1.png" alt="" /> -->
        </div>
        <span>
          {{key.B}}
        </span>
      </div>
      <div class="nav">
        <div class="">
          <input type="radio" name="name" value="">
          <!-- <img src="../../static/img/组-15.png" alt="" /> -->
        </div>
        <span>
          {{key.C}}
        </span>
      </div>
    </div>

    <div class="mat">
      <div class="left">
        <img src="../../static/img/shangH.png" alt="" />
      </div>
      <div class="right">
        <img src="../../static/img/xia.png" alt="" @click="next()" />
      </div>
    </div>

    <div class="progress">
			<div class="progress-bar progress-bar-danger active progress-bar-striped" style="width: 20%">
				<span></span>
			</div>
    	</div>

    </div>
    <div class="footer">
      <img src="../../static/img/foot.png" alt="" />
    </div>
    <cuo :lists="threearr" v-show="threearr[0].cuo"></cuo>
    <dui :lists="threearr" v-show="threearr[0].dui"></dui>
    <kong :lists="threearr" v-show="threearr[0].kong"></kong>
  </div>
</template>

<script>
import cuo from '../module/cuo'
import top from '../module/top'
import dui from "@/module/dui"
import kong from "@/module/kong"
export default {
  data(){
    return{
      threearr:[
        {
          "A":"A、扣除二十元押金。",
          "B":"B、扣除全部押金。",
          "C":"C、扣除20元押金及10分信用积分。",
          "success":"C",
          "url":"/four",
          "cuo":false,
          "kong":false,
          "dui":false
        }
      ]
    }
  },
  components:{
    top,
    cuo,
    kong,
    dui
  },
  methods:{
    next(){
      var nav = document.getElementsByName("name");
        if(nav[0].checked){
          console.log("错误")
          this.threearr[0].cuo = true;
          return;
        }else if(nav[1].checked){
          console.log("错误")
          this.threearr[0].cuo = true;
          return;
        }else if(nav[2].checked){
          console.log("正确")
          this.threearr[0].dui = true;
          return
        }else{
          console.log("空")
          this.threearr[0].kong = true;
          return
        }
      }
    }
}
</script>

<style scoped>
*{
  margin:0;
  padding:0;
}
.app{
	width: 100%;
  padding:0 0.9rem;
}
.content{
  font-size: 0.7rem;
  font-weight: bold;
}
.content p{
  line-height: 1.1rem;
}
.app .img{
  width:100%;
  height:100%;
  padding-bottom: 0.7rem;
  border-bottom:1px solid #f3f3f3;
}
.nav{
  display: flex;
  font-size: 0.7rem;
  border-bottom: 1px solid #f3f3f3;
  padding-top:0.7rem;
  padding-bottom:0.7rem;
  vertical-align: middle;
}
.nav span{
  margin-left: 1.2rem;
}
.nav img{
  margin-left: 0.15rem;
  width:0.6rem;
  height:0.6rem;
  vertical-align: middle;
}
.nav input{
  margin-left: 0.15rem;
  width:0.6rem;
  height:0.6rem;
  vertical-align: middle;
}
.mat{
  display: flex;
  justify-content: space-between;
  padding: 0.9rem 0;
}
.mat img{
  width:6.3rem;
  height:1.9rem;
}
.footer{
  position: fixed;
  bottom:0;
}
.footer img{
  width:100%;
  height:100%;

}
.progress{
	width: 100%;
	margin: 0;
  border-radius: 1.25rem;
  height:0.6rem;
}
/*
.progress-danger{
         background: repeating-linear-gradient(45deg,yellow 10%,#fff 20%);
         border-radius: 1.25rem;
   }
*/
.progress-bar-danger{
		background: repeating-linear-gradient(45deg,yellow 10%,#fff 20%);
         border-radius: 1.25rem;
	}
</style>



