<template lang="html">
  <div class="">
    <top></top>
    <p class="question">
      2、将小蜜蜂停放在公司门口的公共停车区域后，你需要做的第一件事是什么？
    </p>
    <div class="content1">
      <div class="circle">
        <img src="../../static/img/big.png" alt="">
      </div>
    </div>
    <ul class="answer" v-for="(i,index) in twoarr">
      <li class="clearfix">
        <input type="radio" name="click" value="">
        <span>{{i.A}}</span>
      </li>
      <li class="special1 clearfix">
        <input type="radio" name="click" value="">
        <span>{{i.B}}</span>
      </li>
      <li class="special2 clearfix">
        <input type="radio" name="click" value="">
        <span>{{i.C}}</span>
      </li>
    </ul>
    <div class="choose">
      <img src="../../static/img/shangH.png" />
      <img src="../../static/img/xia.png"  @click="next()"/>
    </div>
    <div class="progress">
        <div class="progress-bar progress-danger active progress-bar-striped" style="width: 40%">
            <span></span>
        </div>
    </div>
    <div class="bottom">
      <img src="../../static/img/foot.png" alt="">
    </div>
    <cuo :lists="twoarr" v-show="twoarr[0].cuo"></cuo>
    <dui :lists="twoarr" v-show="twoarr[0].dui"></dui>
    <kong :lists="twoarr" v-show="twoarr[0].kong"></kong>
  </div>
</template>

<script>
import top from '@/module/top'
import cuo from '@/module/cuo'
import dui from '@/module/dui'
import kong from '@/module/kong'
export default {
  data(){
    return{
      twoarr:[
        {
          "A":"A、转身离开去上班。",
          "B":"B、用手机再次打开“蜜蜂出行”",
          "C":"C、长按右侧车把的红色按钮，临时锁车。",
          "success":"C",
          "url":"/three",
          "cuo":false,
          "kong":false,
          "dui":false
        }
      ]
    }
  },
  components:{
    top,
    cuo,
    dui,
    kong
  },
  methods:{
    next(){
      var rad=document.getElementsByName("click")
      if(rad[0].checked){
        console.log(0)
        this.twoarr[0].cuo=true
        return
      }else if(rad[1].checked){
        console.log(0)
        this.twoarr[0].cuo=true
        return
      }else if(rad[2].checked){
        console.log(1)
        this.twoarr[0].dui=true
        return
      }else{
        console.log(2)
        this.twoarr[0].kong=true
        return
      }
    }
  }
}
</script>

<style lang="less" scoped>
  *{
    margin: 0;
    padding: 0;
    list-style: none;
  }
  .question{
    width: 14.2rem;
    padding: 0rem 0.9rem 0.35rem;
/*    font-size: 0.7rem;*/
/*    font-weight: 600;*/
  }
  .content1{
    width: 14.3rem;
    padding: 0 0.85rem;
    .circle{
      height: 6.9rem;
      width: 100%;
      img{
        width: 14.35rem;
        height: 6.8rem;
      }
    }
  }
  .answer{
/*    width: 16rem;*/
    padding: 0.55rem 0.8rem;

    li{
      border-top: 1px solid #eee;
		padding: 0.8rem 0;
      input{
        margin-right: 0.6rem;
		 float: left;
      }

    }
    .special2{
      border-bottom: 1px solid #eee;
    }
	  span{
/*		  width: 100%;*/
/*		  display: inline-block*/
		  float: left
	  }
  }
  .choose{
    width: 16rem;
    padding: 0 0.825rem;
    display: flex;
    justify-content: space-between;
    img{
      width: 6.3rem;
      height: 1.95rem;
    }
  }
  .progress{
    width: 14.4rem;
    margin: 0.6rem 0.8rem;
    border-radius: 1.25rem;
    height:0.6rem;
    .progress-danger{
         background: repeating-linear-gradient(45deg,yellow 10%,#fff 20%);
         border-radius: 1.25rem;
    }
  }
  .bottom{
    width: 16rem;
    height: 2.6rem;
	  position: absolute;
	  bottom: 0;
    img{
      width: 16rem;
      height: 2.6rem;
    }
  }
	.clearfix:after{
		display: block;
		clear: both;
		content: '';
	}
</style>



