<template>
	<div>
		<!--lists数据通过父组件传过来,要想接收到父组件传来的数据，那么需要在props中定义一下-->
		<!--<li v-for="item in lists"></li>-->
		<div class="wrap">
			<div class="bol">
			<img src="../../static/img/cuo.png" alt="">
				<p>很遗憾,正确答案是<span>{{lists[0].success}}</span>。</p>
				<p>将首页地图缩小显示后,将会出现蓝色覆盖<br>
				   区域,该区域即是小蜜蜂的运营区域。只能<br>
				       在运营区域范围内骑行,否则会扣除相应的<br>
                                                  押金及信用积分。
				</p>
				<button @click="click1()">朕知道了</button>
			</div>
		</div>
	</div>
</template>
<script>
	export default {
		props:{
			lists:{
				
			}
		},
		methods:{
			click1(){
				this.lists[0].cuo=false
			}
		}
	}
</script>
<style scoped>
	.wrap{
		width: 100%;
		height:100%;
		background:rgba(0,0,0,0.5);
		position:fixed;
		top:0;
		left:0;
		z-index: 999;
	}
	.bol{
		width:14rem;
		height:14.6rem;
		position: fixed;
		text-align: center;
		top:0;
		left:0;
		bottom:0;
		right:0;
		margin:auto;
	
	}
	.bol>img{
		width: 100%;
		position: absolute;
		top: 0;
		left: 0;
		z-index: -1;
	}
	.bol p:nth-of-type(1){
		font-size: 0.8rem;
		margin-top: 5rem;
		color:#FF2C2C;
		font-weight: bold;
	}
	.bol p:nth-of-type(2){
		font-size: 0.6rem;
		line-height: 1rem;
	}
	.bol button{
		width: 8.4rem;
		height:1.9rem;
		border:none;
		border-radius: 20px;
		font-size: 0.8rem;
		outline: none;
		background: #FFE32D;
	}
</style>


