<template>
	<div>
		<!--lists数据通过父组件传过来,要想接收到父组件传来的数据，那么需要在props中定义一下-->
		<!--<li v-for="item in lists"></li>-->
		<div class="wrap">
			<div class="bol">
			<img src="../../static/img/kong.png" alt="">
				<p>请您先选择一个答案</p>
				<button @click="click2()">朕知道了</button>
			</div>
		</div>
	</div>
</template>
<script>
	export default {
		props:{
			lists:{
				
			}
		},
		methods:{
			click2(){
				this.lists[0].kong=false
			}
		}
	}
</script>
<style scoped>
	.wrap{
		width: 100%;
		height:100%;
		background:rgba(0,0,0,0.5);
		position:fixed;
		top:0;
		left:0;
		z-index: 999;
	}
	.bol{
		width:14rem;
		height:11.6rem;
		position: fixed;
		text-align: center;
		top:0;
		left:0;
		bottom:0;
		right:0;
		margin:auto;
/*
		background:url(../../static/img/kong.png) no-repeat;
		background-size:14rem 11.6rem;
*/
	}
	.bol>img{
		width: 100%;
		position: absolute;
		left: 0;
		top: 1rem;
		z-index: -1;
	}
	.bol p{
		font-size: 1.2rem;
		margin-top: 6rem;
	}
	.bol button{
		width: 8.4rem;
		height:1.9rem;
		border:none;
		border-radius: 20px;
		font-size: 0.8rem;
		outline: none;
		background: #FFE32D;
	}
</style>


